<template>
  <div>
    <h5 class="fw-bold">صندوق پیام ها</h5>
    <div v-if="loadingContacts" class="loading">
      <div class="spinner-grow text-primary" role="status"></div>
      <h4 class="mt-3 fw-bold">درحال بارگزاری</h4>
    </div>
    <table class="table mt-5" v-else>
      <div v-if="Contacts.length == 0" style="margin-top: 100px">
        <h3 class="text-center fw-bold text-primary">پیامی یافت نشد !</h3>
      </div>
      <thead class="table-primary" v-else>
        <tr>
          <th scope="col">شناسه</th>
          <th scope="col">نام ارسال کننده</th>
          <th scope="col">ایمیل</th>
          <th scope="col">تاریخ</th>
          <th scope="col">عملیات</th>
        </tr>
      </thead>
      <tbody class="list">
        <tr v-for="Contact in Contacts" :key="Contact.c_id">
          <th scope="row">{{ Contact.c_id }}</th>
          <td>
            <p class="fw-bold">{{ Contact.c_name }}</p>
          </td>
          <td>
            <p class="fw-bold">{{ Contact.c_email }}</p>
          </td>
          <td>
            <p class="fw-bold">{{ Contact.created_at }}</p>
          </td>
          <td>
            <div class="dropdown">
              <button
                class="btn btn-light-primary dropdown-toggle"
                type="button"
                id="dropdownMenuButton1"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                عملیات
              </button>
              <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                <li>
                  <a
                    class="dropdown-item"
                    @click="doedit(Contact.c_id)"
                    href="#"
                    >ویرایش</a
                  >
                </li>
                <li>
                  <a
                    class="dropdown-item"
                    href="#"
                    @click="deletethis(Contact.c_id)"
                    >حذف</a
                  >
                </li>
              </ul>
            </div>
          </td>
        </tr>
      </tbody>
    </table>

    <div class="mymodal" style="display: block" v-if="modal">
      <div class="modal-dialog" style="margin-top: 100px" v-if="!editloading">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">ویرایش پیام</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
              @click="(modal = !modal), clear()"
            ></button>
          </div>
          <form>
            <div class="modal-body">
              <div class="row gy-4">
                <div class="col-md-6">
                  <label class="form-label">نام</label>
                  <input type="text" class="form-control" v-model="form.name" />
                </div>
                <div class="col-md-6">
                  <label class="form-label">ایمیل</label>
                  <input
                    type="text"
                    class="form-control"
                    v-model="form.email"
                  />
                </div>
                <div class="col-md-6">
                  <label class="form-label">موضوع</label>
                  <input
                    type="text"
                    class="form-control"
                    v-model="form.category"
                  />
                </div>
                <div class="col-md-6">
                  <label class="form-label">آی پی</label>
                  <p>{{ form.ip }}</p>
                </div>
                <div class="col-md-12">
                  <label class="form-label">متن پیام</label>
                  <textarea class="form-control" v-model="form.body"></textarea>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                @click="(modal = !modal), clear()"
              >
                بستن
              </button>
              <button
                type="button"
                class="btn btn-primary"
                @click="doeditcontact(form.id)"
              >
                ویرایش
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref, reactive } from "vue";
import Swal from "sweetalert2";
import { useMeta } from "vue-meta";

export default {
  name: "inbox",
  setup() {
    useMeta({
      title: process.env.VUE_APP_NAME + " | مدیریت پیام ها",
      htmlAttrs: { lang: "FA", amp: true },
      description: "مدیریت پیام ها",
    });
    const Contacts = ref({});
    const loadingContacts = ref(false);
    const modal = ref(false);
    const editloading = ref(false);
    const form = reactive({
      nameMessage: "",
      emailMessage: "",
      bodyMessage: "",
      id: "",
      name: "",
      email: "",
      category: "",
      body: "",
      ip: "",
    });
    const getCookie = (cookie_name) => {
      const re = new RegExp(`(?<=${cookie_name}=)[^;]*`);
      try {
        return document.cookie.match(re)[0]; // Will raise TypeError if cookie is not found
      } catch {
        return "this-cookie-doesn't-exist";
      }
    };
    let token = getCookie("Token");
    function getContact() {
      loadingContacts.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-contact`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          Contacts.value = response.data.data.contact;
          loadingContacts.value = false;
        });
    }
    getContact();

    function doedit(c_id) {
      modal.value = !modal.value;
      editloading.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-contact-byid/${c_id}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          form.name = response.data.data.contact.c_name;
          form.category = response.data.data.contact.c_category;
          form.email = response.data.data.contact.c_email;
          form.body = response.data.data.contact.c_body;
          form.id = response.data.data.contact.c_id;
          form.ip = response.data.data.contact.c_ip;

          editloading.value = false;
        })
        .catch(function (error) {
          console.log(error);
          editloading.value = false;
        });
    }

    function doeditcontact(c_id) {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}edit-contact/${c_id}`,
          {
            name: form.name,
            email: form.email,
            body: form.body,
            category: form.category,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          response;
          modal.value = !modal.value;
          clear();
          getContact();
          Swal.fire({
            icon: "success",
            title: "دسته با موفقیت ویرایش شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          console.log(form);
          Swal.fire({
            icon: "error",
            title: error.message,
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    function clear() {
      form.name = "";
      form.slug = "";
      form.dec = "";
      form.id = "";
    }

    function deletethis(c_id) {
      axios
        .get(`${process.env.VUE_APP_API_URL}delete-contact/${c_id}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          response;
          getContact();
          Swal.fire({
            icon: "success",
            title: "دسته با موفقیت حذف شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          console.log(form);
          Swal.fire({
            icon: "error",
            title: error.message,
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    return {
      Contacts,
      loadingContacts,
      editloading,
      modal,
      form,
      doedit,
      doeditcontact,
      deletethis,
    };
  },
};
</script>

