<template>
  <section class="features py-5 overflow-hidden">
    <div class="container">
      <div
        class="row aos-init aos-animate"
        data-aos="fade-up"
        data-aos-delay="200"
      >
        <h3 class="text-primary fw-bold" data-aos="fade-down">تماس با من</h3>
        <h5 class="text-muted mb-4" data-aos="fade-up">
          تماس با من برای مشاوره و سفارش طراحی
        </h5>
      </div>
      <div
        class="row align-items-center aos-init aos-animate"
        data-aos="fade-up"
        data-aos-delay="400"
      >
        <div class="col-lg-8">
          <form action="#" class="contact-form" @submit.prevent="validate">
            <div class="row gy-4">
              <div class="col-md-6">
                <label class="form-label">نام</label>
                <input class="form-control" type="text" v-model="form.name" />
                <p class="color-danger text-muted">{{ form.NameErorr }}</p>
              </div>

              <div class="col-md-6">
                <label class="form-label">ایمیل</label>
                <input class="form-control" type="email" v-model="form.email" />
                <p class="color-danger text-muted">{{ form.EmailErorr }}</p>
              </div>

              <div class="col-md-12">
                <label class="form-label">موضوع</label>
                <select class="form-control" v-model="form.category">
                  <option value="همکاری" selected>همکاری</option>
                  <option value="طراحی سایت">طراحی سایت</option>
                </select>
                <p class="color-danger text-muted">{{ form.CategoryErorr }}</p>
              </div>

              <div class="col-md-12">
                <label class="form-label">متن پیام</label>
                <textarea
                  class="form-control"
                  rows="2"
                  v-model="form.body"
                ></textarea>
                <p class="color-danger text-muted">{{ form.BodyErorr }}</p>
              </div>

              <div class="col-md-12 text-center">
                <button
                  class="w-100 btn btn-lg text-white btn-primary"
                  style="border-radius: 10px"
                  type="submit"
                  v-if="!loading"
                >
                  ارسال پیام
                </button>
              </div>
              <center>
                <div
                  class="spinner-border text-primary mt-2 mb-3"
                  role="status"
                  v-if="loading"
                ></div>
              </center>
            </div>
          </form>
        </div>

        <div class="col-lg-4 my-4">
          <img
            class="img-fluid"
            src="../../../public/images/undraw_online_connection_6778.png"
            alt=""
            srcset=""
          />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
import { reactive, ref } from "vue";
import Swal from "sweetalert2";
export default {
  name: "connectus",

  setup() {
    const loading = ref(false);
    const form = reactive({
      name: "",
      email: "",
      category: "همکاری",
      body: "",
      CategoryErorr: "",
      NameErorr: "",
      EmailErorr: "",
      BodyErorr: "",
    });

    function validate() {
      if (form.name === "") {
        form.NameErorr = "فیلد نام اجباری است";
      } else {
        form.NameErorr = "";
      }

      if (form.email === "") {
        form.EmailErorr = "ایمیل اجباری است";
      } else {
        form.EmailErorr = "";
      }

      if (form.category === "") {
        form.CategoryErorr = "فیلد موضوع اجباری است";
      } else {
        form.CategoryErorr = "";
      }

      if (form.body === "") {
        form.BodyErorr = "فیلد متن پیام اجباری است";
      } else {
        form.BodyErorr = "";
      }

      if (
        form.name !== "" &&
        form.email !== "" &&
        form.category !== "" &&
        form.body !== ""
      ) {
        loading.value = true;
        create();
      }
    }

    function create() {
      axios
        .post(`${process.env.VUE_APP_API_URL}add-contact`, {
          name: form.name,
          email: form.email,
          category: form.category,
          body: form.body,
        })
        .then(function (response) {
          response;
          (form.name = ""),
            (form.email = ""),
            (form.category = ""),
            (form.body = ""),
            (loading.value = false);
          Swal.fire({
            icon: "success",
            title: "پیام شما دریافت شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          }).catch(function (error) {
            console.log(error);
            loading.value = false;
          });
        });
    }

    return { validate, form, loading };
  },
};
</script>

