<template>
  <div>
    <section class="post-intro d-flex align-items-center overflow-hidden">
      <div class="container">
        <section
          class="post-image"
          v-bind:style="{
            backgroundImage: 'url(' + image + post.p_image + ')',
          }"
          style="
            background-repeat: no-repeat;
            background-size: cover;
            background-overflow: hidden;
          "
        >
          <br /><br /><br /><br /><br /><br /><br /><br /><br /><br />
          <div class="container">
            <div class="row">
              <div class="col-md-12">
                <div class="post-detail">
                  <h2 class="text-white fw-bold">{{ post.p_title }}</h2>
                  <div class="mt-4">
                    <p style="float: right; color: white; display: inline">
                      {{ post.name }}
                    </p>
                    <p style="float: left; color: white; display: inline">
                      {{ moment(post.created_at).format("jYYYY/jM/jD") }}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </section>

    <div class="container my-5 post-body">
      {{ post.p_body }}
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref } from "vue";
import { useRoute } from "vue-router";
import moment from "moment-jalaali";

export default {
  name: "content",
  setup() {
    const post = ref({});
    const route = useRoute();
    const image = process.env.VUE_APP_IMAGES_URL;
    axios
      .get(`${process.env.VUE_APP_API_URL}show-post/${route.params.id}`)
      .then(function (response) {
        post.value = response.data.data.post;
      })
      .catch(function (error) {
        console.log(error);
      });
    return { post, moment, image };
  },
};
</script>

