<template>
  <div>
    <section class="sampels">
      <div class="container">
        <h2 class="text-primary fw-bold text-center mb-5">پست های اخیر</h2>
        <div class="row">
          <div class="col-sm-3 mb-4" v-for="post in posts" :key="post.c_id">
            <div class="card my-card" style="width: 18rem">
              <router-link
                :to="{
                  name: 'post',
                  params: { id: post.p_id },
                }"
              >
                <img v-bind:src="image + post.p_image" class="card-img-top" />
              </router-link>
              <div class="card-body body-texts">
                <div class="row text-muted">
                  <div class="col-md-6">
                    <p class="post-info" style="float: right">
                      {{ post.name }}
                    </p>
                  </div>

                  <div class="col-md-6">
                    <p class="post-info" style="float: left">
                      {{ moment(post.created_at).format("jYYYY/jM/jD") }}
                    </p>
                  </div>
                </div>
                <router-link
                  :to="{
                    name: 'post',
                    params: { id: post.p_id },
                  }"
                >
                  <h5 class="card-title fw-bold text-primary mb-3">
                    {{ post.p_title }}
                  </h5>
                </router-link>
                <p class="card-text post-text text-muted">
                  {{ post.p_body }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { ref } from "vue";
import axios from "axios";
import moment from "moment-jalaali";
export default {
  name: "posts",
  setup() {
    const posts = ref({});
    const image = process.env.VUE_APP_IMAGES_URL;
    axios
      .get(`${process.env.VUE_APP_API_URL}lastposts`)
      .then(function (response) {
        posts.value = response.data.data.post;
        posts.value = response.data.data.post;
      });
    return { posts, image, moment };
  },
};
</script>

