<template>
  <div>
    <h5 class="fw-bold mb-4">مدیریت کاربران</h5>

    <div v-if="userloading" class="loading">
      <div class="spinner-grow text-primary" role="status"></div>
      <h4 class="mt-3 fw-bold">درحال بارگزاری</h4>
    </div>

    <div class="card-body px-0" v-else>
      <div class="verify-content mb-4" v-for="user in users" :key="user.id">
        <div class="d-flex align-items-center">
          <img
            class="ml-3 rounded-circle ml-0 ml-sm-3"
            src="https://www.gravatar.com/avatar/50dcb04ca12e45cc8cc7ac59ebe20a8b?s=100"
            width="55"
            height="55"
          />
          <div class="primary-number">
            <p class="mb-0 fs-9">
              {{ user.name }}
              <sapn class="label-admin" v-if="user.role == 2">مدیر</sapn>
            </p>
            <small class="text-muted">{{ user.email }}</small>
            <br />
            <span
              class="text-danger"
              style="font-size: small"
              v-if="user.status == 2"
              >غیرفعال</span
            >
            <span class="text-success" style="font-size: small" v-else
              >فعال</span
            >
          </div>
        </div>
        <a @click="openmodal(user.id)">
          <button class="btn btn-outline-primary">مدیریت</button>
        </a>
      </div>
    </div>

    <div class="mymodal" style="display: block" v-if="modal">
      <div class="modal-dialog" style="margin-top: 100px" v-if="!formloading">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">ویرایش مطلب</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
              @click="(modal = !modal), clear()"
            ></button>
          </div>
          <form>
            <div class="modal-body">
              <div class="row gy-4">
                <div class="col-md-6">
                  <label class="form-label">نام</label>
                  <input type="text" class="form-control" v-model="form.name" />
                  <p class="color-danger text-muted mt-2">
                    {{ form.namemessage }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">ایمیل</label>
                  <input
                    type="email"
                    class="form-control"
                    v-model="form.email"
                  />
                  <p class="color-danger text-muted mt-2">
                    {{ form.emailmessage }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">دسترسی</label>
                  <select v-model="form.role" class="form-control">
                    <option value="1">کاربر</option>
                    <option value="2">مدیر</option>
                  </select>
                </div>
                <div class="col-md-6">
                  <label class="form-label">وضعیت</label>
                  <select v-model="form.status" class="form-control">
                    <option value="1">فعال</option>
                    <option value="2">غیرفعال</option>
                  </select>
                </div>
                <div class="col-md-6">
                  <label class="form-label">رمزعبور</label>
                  <input
                    type="password"
                    class="form-control"
                    v-model="form.password"
                  />
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                @click="(modal = !modal), clear()"
              >
                بستن
              </button>
              <button type="button" class="btn btn-primary" @click="validate()">
                ویرایش
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { useMeta } from "vue-meta";
import { ref, reactive } from "vue";
import Swal from "sweetalert2";

export default {
  name: "users",
  setup() {
    const getCookie = (cookie_name) => {
      const re = new RegExp(`(?<=${cookie_name}=)[^;]*`);
      try {
        return document.cookie.match(re)[0]; // Will raise TypeError if cookie is not found
      } catch {
        return "this-cookie-doesn't-exist";
      }
    };
    let token = getCookie("Token");
    const users = ref({});
    const userloading = ref(false);
    const formloading = ref(false);

    const modal = ref(false);
    const form = reactive({
      namemessage: "",
      emailmessage: "",
      name: "",
      email: "",
      password: "",
      id: "",
      role: "",
      status: "",
    });
    useMeta({
      title: process.env.VUE_APP_NAME + " |  مدیریت کاربران",
      htmlAttrs: { lang: "FA", amp: true },
      description: "مدیریت کاربران ",
    });

    function validate() {
      if (form.name === "") {
        form.namemessage = "فیلد نام اجباری است";
      } else {
        form.namemessage = "";
      }
      if (form.email === "") {
        form.emailmessage = "فیلد ایمیل اجباری است";
      } else {
        form.emailmessage = "";
      }
      if (form.name !== "" && form.email !== "") {
        usersubmit();
      }
    }

    function usersubmit() {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}edit-user/${form.id}`,
          {
            name: form.name,
            email: form.email,
            password: form.password,
            role: form.role,
            status: form.status,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          response;
          modal.value = !modal.value;
          clear();
          getuser();
          Swal.fire({
            icon: "success",
            title: "کاربر با موفقیت ویرایش شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          }).catch(function (error) {
            console.log(error);
          });
        });
    }

    function getuser() {
      userloading.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-users`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          response;
          users.value = response.data.data.user;
          userloading.value = false;
        })
        .catch(function (erorr) {
          console.log(erorr);
          userloading.value = false;
        });
    }

    getuser();

    function openmodal(id) {
      formloading.value = true;
      clear();
      modal.value = !modal.value;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-user-byid/${id}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          form.id = response.data.data.user.id;
          form.name = response.data.data.user.name;
          form.email = response.data.data.user.email;
          form.role = response.data.data.user.role;
          form.status = response.data.data.user.status;
          formloading.value = false;
        });
    }

    function clear() {
      form.name = "";
      form.email = "";
      form.password = "";
      form.role = "";
      form.status = "";
    }

    return {
      users,
      userloading,
      modal,
      form,
      formloading,
      openmodal,
      validate,
    };
  },
};
</script>

