<template>
  <div>
    <div class="row g-3">
      <div class="col-6 col-xl-3">
        <div class="card">
          <div class="card-body">
            <div class="linear-background" v-if="loading"></div>
            <div class="row g-3" v-else>
              <div class="col-md-4">
                <div class="stats-icon bg-purple">
                  <i class="bi bi-file-check lh-0"></i>
                </div>
              </div>
              <div class="col-md-8">
                <h6 class="fs-7 text-muted">تعداد پست ها</h6>
                <h6
                  class="fw-bold mb-0 purecounter"
                  data-purecounter-start="0"
                  data-purecounter-end="18600"
                >
                  {{ infos.post }}
                </h6>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="col-6 col-xl-3">
        <div class="card">
          <div class="card-body">
            <div class="linear-background" v-if="loading"></div>
            <div class="row g-3" v-else>
              <div class="col-md-4">
                <div class="stats-icon bg-blue">
                  <i class="bi bi-flag lh-0"></i>
                </div>
              </div>
              <div class="col-md-8">
                <h6 class="fs-7 text-muted">دسته بندی ها</h6>
                <h6
                  class="fw-bold mb-0 purecounter"
                  data-purecounter-start="0"
                  data-purecounter-end="126500"
                >
                  {{ infos.category }}
                </h6>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-6 col-xl-3">
        <div class="card">
          <div class="card-body">
            <div class="linear-background" v-if="loading"></div>
            <div class="row g-3" v-else>
              <div class="col-md-4">
                <div class="stats-icon bg-green">
                  <i class="bi bi-envelope lh-0"></i>
                </div>
              </div>
              <div class="col-md-8">
                <h6 class="fs-7 text-muted">پیام ها</h6>
                <h6
                  class="fw-bold mb-0 purecounter"
                  data-purecounter-start="0"
                  data-purecounter-end="95600"
                >
                   {{ infos.contact }}
                </h6>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-6 col-xl-3">
        <div class="card">
          <div class="card-body">
            <div class="linear-background" v-if="loading"></div>
            <div class="row g-3" v-else>
              <div class="col-md-4">
                <div class="stats-icon bg-red">
                  <i class="bi bi-people lh-0"></i>
                </div>
              </div>
              <div class="col-md-8">
                <h6 class="fs-7 text-muted">کاربران</h6>
                <h6
                  class="fw-bold mb-0 purecounter"
                  data-purecounter-start="0"
                  data-purecounter-end="452"
                >
                  {{ infos.User }}
                </h6>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { useMeta } from "vue-meta";
import { ref } from "vue";
export default {
  name: "home",
  setup() {
    useMeta({
      title: process.env.VUE_APP_NAME + " |  داشبورد",
      htmlAttrs: { lang: "FA", amp: true },
      description: "داشبورد",
    });
    const infos = ref({});
    const loading = ref(true);

    axios
      .get(`${process.env.VUE_APP_API_URL}ANALYTICS`)
      .then(function (response) {
        infos.value = response.data.data.infos;
        loading.value = false;
      });
    return { infos, loading };
  },
  data() {
    return {
      open: true,
    };
  },
};
</script>

