import { createRouter, createWebHistory } from 'vue-router';
import axios from "axios";
const getCookie = (cookie_name) => {
    const re = new RegExp(`(?<=${cookie_name}=)[^;]*`);
    try {
        return document.cookie.match(re)[0]; // Will raise TypeError if cookie is not found
    } catch {
        return "this-cookie-doesn't-exist";
    }
};

let token = getCookie("Token");

const routes = [
    //front
    {
        path: '/',
        component: require('../layout/front.vue').default,
        children: [
            {
                path: '',
                component: require('../pages/front/home.vue').default,
                name: 'home'
            },
            {
                path: '/blog',
                component: require('../pages/front/blog.vue').default,
                name: 'blog'
            },
            {
                path: '/post/:id',
                component: require('../pages/front/post.vue').default,
                name: 'post'
            }
        ],
    },


    {
        path: '/login',
        component: require('../pages/auth/login').default,
    },
    {
        path: '/register',
        component: require('../pages/auth/register').default,
    },


    {
        path: '/admin',
        name: 'admin',
        meta: {
            admin: true
        },
        component: require('../layout/admin.vue').default,
        children: [
            {
                path: '',
                component: require('../pages/admin/home').default,
                alias: '/site-admin',
                name: 'admin'
            },
            {
                path: 'posts',
                component: require('../pages/admin/Post/index').default,
                name: 'posts'
            },
            {
                path: 'category',
                component: require('../pages/admin/category/index').default,
                name: 'category'
            },
            {
                path: 'users',
                component: require('../pages/admin/users/index').default,
                name: 'users'
            },
            {
                path: 'inbox',
                component: require('../pages/admin/inbox/index').default,
                name: 'inbox'
            },
        ]
    },

    {
        path: '/:pathMatch(.*)*', component: require('../pages/error/notfound').default, name: '404'
    }


];





const router = createRouter({
    history: createWebHistory(),
    routes,
});


router.beforeEach((routeto, routefrom, next) => {
    if (routeto.meta.admin) {
        axios
            .get(`${process.env.VUE_APP_API_URL}role-check`, {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            })
            .then(function (response) {
                if (routeto.meta.admin && response.data.user == 1) {
                    next('/')
                }
                next()

            })
            .catch(function (error) {
                console.log(error)
                if (routeto.meta.admin && error) {
                    next('/')
                }
                next()

            });
    } else {
        next()
    }
})








export default router;