<template>
  <section>
    <div class="container" style="margin-top: 8%">
      <h1 class="appcolor mr-2 login-app-name">ورود به سایت</h1>
      <h4 class="login-app-dec text-muted">
        جهت استفاده از امکانات سایت وارد شوید
      </h4>
      <form class="form-signin mt-3" @submit.prevent="validate">
        <div class="form-floating mb-2">
          <input
            type="text"
            class="form-control"
            placeholder="name@example.com"
            v-model="form.email"
          />
          <label for="floatingInput">آدرس ایمیل</label>
        </div>
        <p class="color-danger text-muted">{{ form.emailErrorText }}</p>

        <div class="form-floating mb-2">
          <input
            type="Password"
            class="form-control"
            placeholder="Password"
            v-model="form.password"
          />
          <label for="floatingInput">رمز عبور</label>
        </div>
        <p class="color-danger text-muted">{{ form.passErrorText }}</p>

        <button
          class="w-100 btn btn-lg mb-3 text-white"
          style="border-radius: 10px; background-color: #00cc93"
          type="submit"
          v-if="!loading"
        >
          ورود
        </button>

        <center>
          <div
            class="spinner-border text-success mt-2 mb-3"
            role="status"
            v-if="loading"
          ></div>
        </center>

        <p class="appcolor">
          اکانت نداری ؟
          <router-link to="register" class="fw-bold">ثبت نام کنید</router-link>
        </p>
      </form>
    </div>
  </section>
</template>

<script>
import axios from "axios";
import { reactive, ref } from "vue";
import router from "../../router/routes";
import Swal from "sweetalert2";
import { useMeta } from "vue-meta";

export default {
  name: "login",
  setup() {
    useMeta({
      title: process.env.VUE_APP_NAME + " |  ورود",
      htmlAttrs: { lang: "FA", amp: true },
      description:
        "ورود به سایت",
    });

    const loading = ref(false);
    const form = reactive({
      email: "",
      emailErrorText: "",
      passErrorText: "",
      password: "",
    });

    function validate() {
      if (form.password === "") {
        form.passErrorText = "پسورد اجباری است";
      } else {
        form.passErrorText = "";
      }

      if (form.email === "") {
        form.emailErrorText = "ایمیل اجباری است";
      } else {
        form.emailErrorText = "";
      }

      if (form.email !== "" && form.password !== "") {
        loading.value = true;
        login();
      }
    }

    const Toast = Swal.mixin({
      toast: true,
      position: "top",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    function login() {
      axios
        .post(`${process.env.VUE_APP_API_URL}login`, {
          email: form.email,
          password: form.password,
        })
        .then(function (response) {
          loading.value = false;
          let d = new Date();
          d.setTime(d.getTime() + 1 * 24 * 60 * 60 * 1000);
          let expires = "expires=" + d.toUTCString();
          document.cookie =
            "Token=" + response.data.data.token + ";" + expires + ";path=/";
          Swal.fire({
            icon: "success",
            title: "با موفقیت وارد شدید",
            toast: true,
            position: "top",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
          router.replace("/");
        })
        .catch(function (error) {
          loading.value = false;
          if (error.message === "Network Error") {
            Swal.fire({
              icon: "error",
              title: "can’t connect to server, check internet connection",
              toast: true,
              position: "top",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
            });
          }
          Swal.fire({
            icon: "error",
            title: error.response.data.message,
            toast: true,
            position: "top",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
          });
        });
    }
    return { validate, login, loading, form, Toast };
  },
};
</script>

