<template>
  <section>
    <div class="container" style="margin-top: 8%">
      <h1 class="appcolor mr-2 login-app-name">ثبت نام در سایت</h1>
      <h4 class="login-app-dec text-muted">
        جهت استفاده از امکانات سایت ثبت نام کنید
      </h4>
      <form class="form-signin mt-3" @submit.prevent="validate">
        <div class="form-floating mb-2">
          <input
            type="text"
            class="form-control"
            placeholder="User name"
            v-model="form.name"
          />
          <label for="floatingInput">نام و نام خانوادگی</label>
        </div>
        <p class="color-danger text-muted">{{ form.nameErrorText }}</p>

        <div class="form-floating mb-2">
          <input
            type="text"
            class="form-control"
            placeholder="name@example.com"
            v-model="form.email"
            autocomplete="false"
          />
          <label for="floatingInput">ادرس ایمیل</label>
        </div>
        <p class="color-danger text-muted">{{ form.emailErrorText }}</p>

        <div class="form-floating mb-2">
          <input
            type="Password"
            class="form-control"
            placeholder="Password"
            v-model="form.password"
            autocomplete="false"
          />
          <label for="floatingInput">رمز عبور</label>
        </div>
        <p class="color-danger text-muted">{{ form.passErrorText }}</p>

        <div class="form-floating mb-3">
          <input
            type="Password"
            class="form-control"
            placeholder="Re-Password"
            v-model="form.c_password"
            autocomplete="false"
          />
          <label for="floatingInput">تکرار رمز عبور</label>
        </div>
        <p class="color-danger text-muted">{{ form.c_passwordErrorText }}</p>

        <button
          class="w-100 btn btn-lg mb-3 text-white"
          style="border-radius: 10px; background-color: #00cc93"
          type="submit"
          v-if="!loading"
        >
          ثبت نام
        </button>

        <center>
          <div
            class="spinner-border text-success mt-2 mb-3"
            role="status"
            v-if="loading"
          ></div>
        </center>
        <p class="appcolor">
          اکانت دارید ؟ ?
          <router-link to="login" class="fw-bold">وارد شوید</router-link>
        </p>
      </form>
    </div>
  </section>
</template>

<script>
import axios from "axios";
import { reactive, ref } from "vue";
import router from "../../router/routes";
import Swal from "sweetalert2";
import { useMeta } from "vue-meta";

export default {
  name: "register",

  setup() {
    useMeta({
      title: process.env.VUE_APP_NAME + " |  ثبت نام",
      htmlAttrs: { lang: "FA", amp: true },
      description: "ثبت نام در سایت",
    });

    const loading = ref(false);
    const form = reactive({
      name: "",
      email: "",
      password: "",
      c_password: "",
      emailErrorText: "",
      passErrorText: "",
      nameErrorText: "",
      c_passwordErrorText: "",
    });

    function validate() {
      if (form.password === "") {
        form.passErrorText = "رمز عبور اجباری است";
      } else {
        form.passErrorText = "";
      }

      if (form.password.length < 8) {
        form.passErrorText = "پسورد باید بیشتر از 8 کارکتر باشد";
      } else {
        form.passErrorText = "";
      }

      if (form.email === "") {
        form.emailErrorText = "ایمیل اجباری است";
      } else {
        form.emailErrorText = "";
      }

      if (form.name === "") {
        form.nameErrorText = "نام اجباری است";
      } else {
        form.nameErrorText = "";
      }

      if (form.password !== form.c_password) {
        form.c_passwordErrorText = "رمز عبور و تکرار رمز عبور یکسان نیست";
      } else {
        form.c_passwordErrorText = "";
      }

      if (
        form.email !== "" &&
        form.password !== "" &&
        form.name !== "" &&
        form.c_password !== "" &&
        form.password === form.c_password
      ) {
        loading.value = true;
        regsiter();
      }

      function regsiter() {
        axios
          .post(`${process.env.VUE_APP_API_URL}register`, {
            name: form.name,
            email: form.email,
            password: form.password,
            c_password: form.c_password,
          })
          .then(function (response) {
            loading.value = false;
            let d = new Date();
            d.setTime(d.getTime() + 1 * 24 * 60 * 60 * 1000);
            let expires = "expires=" + d.toUTCString();
            document.cookie =
              "Token=" + response.data.data.token + ";" + expires + ";path=/";
            Swal.fire({
              icon: "success",
              title: "اکانت شما با موفقیت ساخته شد !",
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 4000,
              timerProgressBar: true,
            });
            router.replace("/");
          })
          .catch(function (error) {
            loading.value = false;
            if (error.message === "Network Error") {
              Swal.fire({
                icon: "error",
                title: "can’t connect to server, check internet connection",
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
              });
            }
            if ((error.response.data.message.email === undefined) === false) {
              Swal.fire({
                icon: "error",
                title: error.response.data.message.email,
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
              });
            }

            if (
              (error.response.data.message.password === undefined) ===
              false
            ) {
              Swal.fire({
                icon: "error",
                title: error.response.data.message.password,
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
              });
            }

            if ((error.response.data.message.name === undefined) === false) {
              Swal.fire({
                icon: "error",
                title: error.response.data.message.name,
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
              });
            }
          });
      }
    }
    return { validate, form, loading };
  },
};
</script>

