<template>
  <div class="container mt-3">
    <div class="row">
      <div class="col-md-6">
        <h5 class="fw-bold">دسته بندی مطالب</h5>
      </div>
      <div class="col-md-6">
        <button
          type="button"
          style="float: left"
          class="btn btn-primary"
          data-bs-toggle="modal"
          data-bs-target="#exampleModal"
        >
          افزودن
        </button>
      </div>
    </div>

    <div v-if="thisloading" class="loading">
      <div class="spinner-grow text-primary" role="status"></div>
      <h4 class="mt-3 fw-bold">درحال بارگزاری</h4>
    </div>

    <div class="table" v-else>
      <div v-if="showcategory.length == 0" style="margin-top: 100px">
        <h3 class="text-center fw-bold text-primary">دسته ای یافت نشد !</h3>
      </div>

      <table class="table mt-5" v-else>
        <thead class="table-primary">
          <tr>
            <th scope="col">شناسه</th>
            <th scope="col">نام</th>
            <th scope="col">slug</th>
            <th scope="col">عملیات</th>
          </tr>
        </thead>
        <tbody class="list">
          <tr v-for="category in showcategory" :key="category.c_id">
            <th scope="row">{{ category.c_id }}</th>
            <td>
              <p class="fw-bold">{{ category.c_name }}</p>
            </td>
            <td>
              <p class="fw-bold">{{ category.c_slug }}</p>
            </td>
            <td>
              <div class="dropdown">
                <button
                  class="btn btn-light-primary dropdown-toggle"
                  type="button"
                  id="dropdownMenuButton1"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  عملیات
                </button>
                <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                  <li>
                    <a
                      class="dropdown-item"
                      @click="edite(category.c_id)"
                      href="#"
                      >ویرایش</a
                    >
                  </li>
                  <li>
                    <a
                      class="dropdown-item"
                      href="#"
                      @click="deletethis(category.c_id)"
                      >حذف</a
                    >
                  </li>
                </ul>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Modal -->
    <div
      class="modal fade mt-5"
      id="exampleModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">افزودن دسته بندی</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <form>
            <div class="modal-body">
              <div class="row gy-4">
                <div class="col-md-6">
                  <label class="form-label">نام دسته</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.name"
                  />
                  <p class="color-danger text-muted mt-2">
                    {{ form.nameMessage }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">slug</label>
                  <input class="form-control" type="text" v-model="form.slug" />
                  <p class="color-danger text-muted mt-2">
                    {{ form.slugMessage }}
                  </p>
                </div>

                <div class="col-md-12">
                  <label class="form-label">توضیحات دسته</label
                  ><textarea
                    class="form-control"
                    rows="2"
                    v-model="form.dec"
                  ></textarea>
                  <p class="color-danger text-muted mt-2">
                    {{ form.decMessage }}
                  </p>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                بستن
              </button>
              <button type="button" class="btn btn-primary" @click="validate">
                انتشار
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>

    <div class="mymodal" style="display: block" v-if="modal">
      <div class="modal-dialog" style="margin-top: 100px" v-if="!editloading">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">ویرایش مطلب</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
              @click="(modal = !modal), clear()"
            ></button>
          </div>
          <form>
            <div class="modal-body">
              <div class="row gy-4">
                <div class="col-md-6">
                  <label class="form-label">عنوان</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.name"
                  />
                  <p class="color-danger text-muted mt-2">
                    {{ form.nameMessage }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">Slug</label>
                  <input class="form-control" type="text" v-model="form.slug" />
                  <p class="color-danger text-muted mt-2">
                    {{ form.slugMessage }}
                  </p>
                </div>

                <div class="col-md-12">
                  <label class="form-label">توضیحات دسته</label
                  ><textarea
                    class="form-control"
                    rows="2"
                    v-model="form.dec"
                  ></textarea>
                  <p class="color-danger text-muted mt-2">
                    {{ form.decMessage }}
                  </p>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                @click="(modal = !modal), clear()"
              >
                بستن
              </button>
              <button
                type="button"
                class="btn btn-primary"
                @click="doedit(form.id)"
              >
                ویرایش
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref, reactive } from "vue";
import Swal from "sweetalert2";
import { useMeta } from "vue-meta";

export default {
  name: "category-index",
  setup() {
    useMeta({
      title: process.env.VUE_APP_NAME + " |  مدیریت دسته بندی",
      htmlAttrs: { lang: "FA", amp: true },
      description: "مدیریت دسته بندی",
    });
    const modal = ref(false);
    const thisedit = ref({});

    const editloading = ref(false);

    const loading = ref(false);
    const thisloading = ref(false);
    const form = reactive({
      nameMessage: "",
      slugMessage: "",
      decMessage: "",
      id: "",
      name: "",
      slug: "",
      dec: "",
    });
    // function uploadImage(e) {
    //   loading.value = true;
    //   let img = e.target.files[0];
    //   let fd = new FormData();
    //   fd.append("image", img);
    //   axios
    //     .post(`${process.env.VUE_APP_API_URL}upload-image`, fd)
    //     .then(function (response) {
    //       response;
    //       form.imageMessage = "تصویر با موفقیت آپلود شد";
    //       form.image = response.data.data.image;
    //     })
    //     .catch(function (error) {
    //       form.imageMessage = error.response.data.message.image;
    //     });
    //   if (loading.value == true) {
    //     form.imageMessage = "درحال آپلود";
    //   }
    // }
    function validate() {
      if (form.name === "") {
        form.nameMessage = "فیلد نام دسته اجباری است";
      } else {
        form.nameMessage = "";
      }
      if (form.slug === "") {
        form.slugMessage = "فیلد slug اجباری است";
      } else {
        form.slugMessage = "";
      }
      if (form.dec === "") {
        form.decMessage = "توضحیات دسته اجباری است";
      } else {
        form.decMessage = "";
      }
      if (form.name !== "" && form.slug !== "" && form.dec !== "") {
        loading.value = true;
        create();
      }
    }
    const getCookie = (cookie_name) => {
      const re = new RegExp(`(?<=${cookie_name}=)[^;]*`);
      try {
        return document.cookie.match(re)[0]; // Will raise TypeError if cookie is not found
      } catch {
        return "this-cookie-doesn't-exist";
      }
    };
    let token = getCookie("Token");
    function create() {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}create-category`,
          {
            name: form.name,
            slug: form.slug,
            dec: form.dec,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          response;
          get();
          Swal.fire({
            icon: "success",
            title: "دسته با موفقیت ثبت شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          Swal.fire({
            icon: "error",
            title: error.response,
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }
    const showcategory = ref([]);
    function get() {
      thisloading.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-categories`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          showcategory.value = response.data.data.categories;
          thisloading.value = false;
        })
        .catch(function (error) {
          error;
          thisloading.value = false;
        });
    }

    function deletethis(c_id) {
      axios
        .get(`${process.env.VUE_APP_API_URL}delete-category/${c_id}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          response;
          get();
          Swal.fire({
            icon: "success",
            title: "دسته با موفقیت حذف شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          console.log(error);
        });
    }

    function edite(c_id) {
      modal.value = !modal.value;
      editloading.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-category-byid/${c_id}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          form.name = response.data.data.category.c_name;
          form.slug = response.data.data.category.c_slug;
          form.dec = response.data.data.category.c_description;
          form.id = response.data.data.category.c_id;
          editloading.value = false;
        })
        .catch(function (error) {
          console.log(error);
          editloading.value = false;
        });
    }

    function doedit(c_id) {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}edit-category/${c_id}`,
          {
            name: form.name,
            slug: form.slug,
            dec: form.dec,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          response;
          modal.value = !modal.value;
          clear();
          get();
          Swal.fire({
            icon: "success",
            title: "دسته با موفقیت ویرایش شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          console.log(form);
          Swal.fire({
            icon: "error",
            title: error.message,
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    function clear() {
      form.name = "";
      form.slug = "";
      form.dec = "";
      form.id = "";
    }

    get();
    return {
      form,
      // uploadImage,
      validate,
      create,
      get,
      edite,
      deletethis,
      clear,
      doedit,
      editloading,
      thisedit,
      modal,
      showcategory,
      loading,
      thisloading,
    };
  },
};
</script>

