<template>
  <div class="container mt-3">
    <div class="row">
      <div class="col-md-6">
        <h5 class="fw-bold">مطالب منتشر شده در سایت</h5>
      </div>
      <div class="col-md-6">
        <button
          type="button"
          style="float: left"
          class="btn btn-primary"
          data-bs-toggle="modal"
          data-bs-target="#exampleModal"
        >
          افزودن
        </button>
      </div>
    </div>

    <div v-if="postloading" class="loading">
      <div class="spinner-grow text-primary" role="status"></div>
      <h4 class="mt-3 fw-bold">درحال بارگزاری</h4>
    </div>

    <div class="table" v-else>
      <div v-if="showpost.length == 0" style="margin-top: 100px">
        <h3 class="text-center fw-bold text-primary">پستی یافت نشد !</h3>
      </div>

      <table class="table mt-5" v-else>
        <thead class="table-primary">
          <tr>
            <th scope="col">شناسه</th>
            <th scope="col">عنوان</th>
            <th scope="col">دسته بندی</th>
            <th scope="col">وضعیت</th>
            <th scope="col">تصویر</th>
            <th scope="col">عملیات</th>
          </tr>
        </thead>
        <tbody class="list">
          <tr v-for="post in showpost" :key="post.id">
            <th scope="row">{{ post.p_id }}</th>
            <td>
              <p class="fw-bold">{{ post.p_title }}</p>
            </td>
            <td>
              <p class="fw-bold">{{ post.c_name }}</p>
            </td>
            <td>
              <center>
                <p class="label-success" v-if="post.p_status == 1">فعال</p>
                <p class="label-danger" v-else>غیرفعال</p>
              </center>
            </td>
            <td>
              <img
                v-bind:src="'http://localhost:8000/' + 'images/' + post.p_image"
                class="image-list"
              />
            </td>
            <td>
              <div class="dropdown">
                <button
                  class="btn btn-light-primary dropdown-toggle"
                  type="button"
                  id="dropdownMenuButton1"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  عملیات
                </button>
                <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                  <li><a class="dropdown-item" href="#">نمایش</a></li>
                  <li>
                    <a class="dropdown-item" @click="edite(post.p_id)" href="#"
                      >ویرایش</a
                    >
                  </li>
                  <li>
                    <a
                      class="dropdown-item"
                      href="#"
                      @click="deletepost(post.p_id)"
                      >حذف</a
                    >
                  </li>
                </ul>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Modal -->
    <div
      class="modal fade mt-5"
      id="exampleModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">افزودن مطلب</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <form>
            <div class="modal-body">
              <div class="row gy-4">
                <div class="col-md-6">
                  <label class="form-label">عنوان</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.title"
                  />
                  <p class="color-danger text-muted mt-2">
                    {{ form.titleMessage }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">وضعیت</label
                  ><select class="form-control" v-model="form.status">
                    <option value="1" selected>فعال</option>
                    <option value="2">غیرفعال</option>
                  </select>
                  <p class="color-danger text-muted mt-2">
                    {{ form.statusMessage }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">دسته بندی</label
                  ><select class="form-control" v-model="form.category">
                    <option
                      :value="cat.c_id"
                      v-for="cat in categories"
                      :key="cat.c_id"
                    >
                      {{ cat.c_name }}
                    </option>
                  </select>
                </div>
                <div class="col-md-6">
                  <label class="form-label">تصویر</label
                  ><input
                    class="form-control"
                    type="file"
                    @change="uploadImage"
                    name="image"
                    id="image"
                    accept="image/*"
                  />
                  <p class="color-danger text-muted mt-2">
                    {{ form.imageMessage }}
                  </p>
                </div>

                <div class="col-md-12">
                  <label class="form-label">متن نوشته</label
                  ><textarea
                    class="form-control"
                    rows="2"
                    v-model="form.body"
                  ></textarea>
                  <p class="color-danger text-muted mt-2">
                    {{ form.bodyMessage }}
                  </p>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                بستن
              </button>
              <button type="button" class="btn btn-primary" @click="validate">
                انتشار
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>

    <div class="mymodal" style="display: block" v-if="modal">
      <div class="modal-dialog" style="margin-top: 100px" v-if="!editloading">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">ویرایش مطلب</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
              @click="(modal = !modal), clear()"
            ></button>
          </div>
          <form>
            <div class="modal-body">
              <div class="row gy-4">
                <div class="col-md-6">
                  <label class="form-label">عنوان</label
                  ><input
                    class="form-control"
                    type="text"
                    v-model="form.title"
                  />
                  <p class="color-danger text-muted mt-2">
                    {{ form.titleMessage }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">وضعیت</label
                  ><select class="form-control" v-model="form.status">
                    <option value="1" selected>فعال</option>
                    <option value="2">غیرفعال</option>
                  </select>
                  <p class="color-danger text-muted mt-2">
                    {{ form.statusMessage }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">تصویر</label
                  ><input
                    class="form-control"
                    type="file"
                    @change="uploadImage"
                    name="image"
                    id="image"
                    accept="image/*"
                  />
                  <p class="color-danger text-muted mt-2">
                    {{ form.imageMessage }}
                  </p>
                </div>
                <div class="col-md-6">
                  <label class="form-label">دسته بندی</label
                  ><select class="form-control" v-model="form.category">
                    <option
                      :value="cat.c_id"
                      v-for="cat in categories"
                      :key="cat.c_id"
                    >
                      {{ cat.c_name }}
                    </option>
                  </select>
                </div>

                <div class="col-md-12">
                  <label class="form-label">متن نوشته</label
                  ><textarea
                    class="form-control"
                    rows="2"
                    v-model="form.body"
                  ></textarea>
                  <p class="color-danger text-muted mt-2">
                    {{ form.bodyMessage }}
                  </p>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                @click="(modal = !modal), clear()"
              >
                بستن
              </button>
              <button
                type="button"
                class="btn btn-primary"
                @click="doedit(form.id)"
              >
                ویرایش
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref, reactive } from "vue";
import Swal from "sweetalert2";
import { useMeta } from "vue-meta";

export default {
  name: "Post-index",
  setup() {
    useMeta({
      title: process.env.VUE_APP_NAME + " |  مدیریت مطالب",
      htmlAttrs: { lang: "FA", amp: true },
      description: "مدیریت مطالب سایت",
    });
    const modal = ref(false);
    const postedit = ref({});
    const categories = ref({});
    const editloading = ref(false);
    const loading = ref(false);
    const postloading = ref(false);
    const form = reactive({
      titleMessage: "",
      imageMessage: "",
      bodyMessage: "",
      statusMessage: "",
      id: "",
      category: "",
      title: "",
      image: "",
      status: "1",
      body: "",
    });

    function uploadImage(e) {
      loading.value = true;
      let img = e.target.files[0];
      let fd = new FormData();
      fd.append("image", img);
      axios
        .post(`${process.env.VUE_APP_API_URL}upload-image`, fd)
        .then(function (response) {
          response;
          form.imageMessage = "تصویر با موفقیت آپلود شد";
          form.image = response.data.data.image;
        })
        .catch(function (error) {
          form.imageMessage = error.response.data.message.image;
        });
      if (loading.value == true) {
        form.imageMessage = "درحال آپلود";
      }
    }
    function validate() {
      if (form.title === "") {
        form.titleMessage = "فیلد عنوان اجباری است";
      } else {
        form.titleMessage = "";
      }
      if (form.image === "") {
        form.imageMessage = "تصویر خود را آپلود کنید";
      } else {
        form.imageMessage = "";
      }
      if (form.body === "") {
        form.bodyMessage = "متن پست اجباری است";
      } else {
        form.bodyMessage = "";
      }
      if (form.status === "") {
        form.statusMessage = "یک گزینه را انتخاب کنید";
      } else {
        form.statusMessage = "";
      }

      if (form.title !== "" && form.image !== "" && form.body !== "") {
        loading.value = true;
        createpost();
      }
    }
    const getCookie = (cookie_name) => {
      const re = new RegExp(`(?<=${cookie_name}=)[^;]*`);
      try {
        return document.cookie.match(re)[0]; // Will raise TypeError if cookie is not found
      } catch {
        return "this-cookie-doesn't-exist";
      }
    };
    let token = getCookie("Token");

    function getcategories() {
      axios
        .get(`${process.env.VUE_APP_API_URL}show-categories`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          categories.value = response.data.data.categories;
        })
        .catch(function (error) {
          console.log(error);
        });
    }

    getcategories();

    function createpost() {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}create-post`,
          {
            title: form.title,
            status: form.status,
            body: form.body,
            category: form.category,
            image: form.image,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          response;
          getposts();
          Swal.fire({
            icon: "success",
            title: "پست با موفقیت ثبت شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          Swal.fire({
            icon: "error",
            title: "ارور کد :" + error.response.status,
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }
    const showpost = ref([]);
    function getposts() {
      postloading.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-post`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          showpost.value = response.data.data.post;
          postloading.value = false;
        })
        .catch(function (error) {
          error;
          postloading.value = false;
        });
    }

    function deletepost(p_id) {
      axios
        .get(`${process.env.VUE_APP_API_URL}delete-post/${p_id}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          response;
          getposts();
          Swal.fire({
            icon: "success",
            title: "پست با موفقیت حذف شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          console.log(error);
        });
    }

    function edite(p_id) {
      modal.value = !modal.value;
      editloading.value = true;
      axios
        .get(`${process.env.VUE_APP_API_URL}show-post-byid/${p_id}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(function (response) {
          form.title = response.data.data.post.p_title;
          form.body = response.data.data.post.p_body;
          form.status = response.data.data.post.p_status;
          form.id = response.data.data.post.p_id;
          form.category = response.data.data.post.p_category;

          editloading.value = false;
        })
        .catch(function (error) {
          console.log(error);
          editloading.value = false;
        });
    }

    function doedit(p_id) {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}edit-post/${p_id}`,
          {
            title: form.title,
            status: form.status,
            body: form.body,
            category: form.category,
            image: form.image,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          response;
          modal.value = !modal.value;
          clear();
          getposts();
          Swal.fire({
            icon: "success",
            title: "پست با موفقیت ویرایش شد",
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        })
        .catch(function (error) {
          console.log(form);
          Swal.fire({
            icon: "error",
            title: error.message,
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 4000,
            timerProgressBar: true,
          });
        });
    }

    function clear() {
      form.title = "";
      form.body = "";
      form.status = "";
      form.id = "";
    }

    getposts();
    return {
      form,
      uploadImage,
      validate,
      createpost,
      getposts,
      edite,
      deletepost,
      clear,
      doedit,
      categories,
      editloading,
      postedit,
      modal,
      showpost,
      loading,
      postloading,
    };
  },
};
</script>

